.worklist {
  width: 100%;
}

.worklist ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 24px;
}

.worklist li a {
  display: block;
  background: #fff;
  border: 1px solid #eee;
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
  transition: transform 120ms ease, box-shadow 120ms ease;
}

.cover img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.workbrief {
  padding: 14px;
}

.workbrief h3 {
  margin: 0 0 6px;
  color: #222;
}

.workbrief p {
  margin: 0 0 8px;
  color: #555;
}

.tag span {
  display: inline-block;
  margin-right: 6px;
  padding: 2px 8px;
  font-size: 12px;
  color: #333;
  background: #f3f3f3;
  border-radius: 999px;
}

.worklist li a:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0,0,0,0.06);
}

.worklist {
  ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    li {
      width: 45%;
      margin-bottom: 45px;
      a {
        display: inline-flex;
        justify-content: space-between;
        .cover {
          img {
            width: 100px;
            border-radius: 5px;
            box-shadow: 2px 2px 2px rgba(53, 52, 52, 0.6);
          }
          img:hover {
            width: 102px;
            border-radius: 5px;
          }

        }
        .workbrief {
          text-align: left;
          padding-left: 15px;
          h3 {
            font-size: 18px;
            color: rgb(51, 51, 51);
          }
          p {
            padding: 8px 0px;
            color: rgba(0, 0, 0, 0.7);
            line-height: 1.8;
          }
          .tag {
            display: flex;
            justify-content: flex-start;
            flex-wrap: wrap;
            span {
              color: rgba(0, 0, 0, 0.55);
              font-size: 14px;
            }
            span:not(:last-child)::after {
              content: "â€¢";
              padding: 0px 0.5em;
            }
          }
        }
      }
    }
  }
}
